<template>
  <div class="charface" :class="{ reversed: reversed }">
    <img
      v-if="pulsate"
      class="pulsate-once"
      :src="`/img/game/${facepic}.jpg`"
    />
    <img v-if="isTarget" class="pulsate" :src="`/img/game/${facepic}.jpg`" />
    <img :src="`/img/game/${facepic}.jpg`" :class="{ fainted: ko }" />
  </div>
</template>

<script>
export default {
  props: ["facepic", "ko", "isTarget", "reverse", "index"],
  data() {
    return { pulsate: false };
  },
  computed: {
    reversed() {
      return this.reverse;
    },
  },
  created() {
    this.$eventBus.$on(`healthLost-${this.index}`, () => {
      this.pulsate = true;
      setTimeout(()=>{
        this.pulsate = false;
      }, 1000)
    });
  },
};
</script>

<style lang='scss'>
.charface {
  width: 75px;
  height: 75px;
  position: relative;
  border: 2px solid #fafafa;
  box-shadow: 1px 1px 2px 2px rgba($color: #000000, $alpha: 0.2);
  border-radius: 15px;

  img {
    border-radius: 15px;
  }
}
.reversed {
  transform: scaleX(-1);
}
</style>